<template>
    <div>
        <TopNav title="添加满减"></TopNav>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
                    <p class="tit">1)满减产品包邮以 关联产品为准</p>
                </div>
                <el-form :model="goodsDetail" ref="goodsInfo" size="mini" label-width="120px">
                    <div class="wyu-title">
                        <h2>选择产品</h2>
                    </div>
                    <el-form-item label="选择产品">
                        <product-select @selGoods="getGoods" :prom_type='0'></product-select>
                    </el-form-item>
                    <el-form-item>
                        <el-table size="mini" :data="selGoods" row-key="key" height="250" border style="width: 800px">
                            <el-table-column label="商品ID" prop="goods_id" width="100"></el-table-column>
                            <el-table-column label="商品编号" prop="goods_sn" width="100"></el-table-column>
                            <el-table-column label="缩略图" width="60">
                                <template slot-scope="scope">
                                    <img width="40" height="40" :src="scope.row.photo"/>
                                </template>
                            </el-table-column>
                            <el-table-column label="商品名称" prop="goods_name">
                                <template slot-scope="scope">
                                    <div class="ellipsis-02">
                                        {{scope.row.goods_name}}
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column label="价格" prop="shop_price" width="100"></el-table-column>
                            <el-table-column label="操作" width="100">
                                <template slot-scope="scope">
                                    <el-button type="text" @click="remove(scope)">移除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-form-item>
                    <div class="wyu-title">
                        <h2>基本信息</h2>
                    </div>
                    <el-form-item label="满减活动名称">
                        <el-input v-model="goodsDetail.name" style="width: 500px;"></el-input>
                    </el-form-item>

                    <el-form-item label="活动封面图片">
                        <wyu-upload :img="goodsDetail.prom_img" v-model="goodsDetail.prom_img"></wyu-upload>
                        <p class="tit"> 建议尺寸750px*396px或等比缩放</p>
                    </el-form-item>

                    <el-form-item label="满">
                        <el-input v-model="goodsDetail.man" style="width: 500px;"></el-input>
                    </el-form-item>

                    <el-form-item label="减">
                        <el-input v-model="goodsDetail.jian" style="width: 500px;"></el-input>
                    </el-form-item>

                    <el-form-item label="PLUS会员满减">
                        <el-switch v-model="goodsDetail.is_vip" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>

                    <el-form-item label="PLUS会员满" v-if="goodsDetail.is_vip == 1">
                        <el-input v-model="goodsDetail.vip_man" style="width: 500px;"></el-input>
                    </el-form-item>

                    <el-form-item label="PLUS会员减" v-if="goodsDetail.is_vip == 1">
                        <el-input v-model="goodsDetail.vip_jian" style="width: 500px;"></el-input>
                    </el-form-item>

                    <el-form-item label="满减开始时间">
                        <el-date-picker v-model="goodsDetail.start_time" type="datetime" placeholder="选择日期时间"
                                        align="right"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="满减结束时间">
                        <el-date-picker v-model="goodsDetail.end_time" type="datetime" placeholder="选择日期时间"
                                        align="right"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="排序">
                        <el-input v-model="goodsDetail.orderby" style="width: 500px;"></el-input>
                    </el-form-item>

                    <el-form-item label="是否开启">
                        <el-switch v-model="goodsDetail.is_online" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>


                    <div class="wyu-title">
                        <h2>SEO设置</h2>
                    </div>
                    <el-form-item label="百度SEO标题" label-width="120px">
                        <el-input v-model="goodsDetail.seo.title" style="width:500px;"  autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO描述" label-width="120px">
                        <el-input type="textarea" :autosize="{minRows: 2, maxRows: 5}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" v-model="goodsDetail.seo.keywords"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO关键词" label-width="120px">
                        <el-input type="textarea" :autosize="{minRows: 5, maxRows: 10}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" v-model="goodsDetail.seo.description"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO原始发布时间" label-width="120px">
                        <el-date-picker v-model="goodsDetail.seo.releaseDate" type="date" placeholder="选择日期"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="百度封面图片" label-width="120px">
                        <img-uploads :multi="true" :img="goodsDetail.seo.image"  v-model="goodsDetail.seo.image"></img-uploads>
                        <p class="tit"> 用于信息流投放后的封面显示，最多3张，单图片最大2M；封面图建议尺寸：高>=210px 宽>=375px;最小尺寸: 高>=146px 宽>=218px</p>
                    </el-form-item>

                </el-form>
            </div>
        </div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary" @click="createGoods()">确认提交</el-button>
            <router-link to="/marketing/manjian/index" style="margin-left: 10px">
                <el-button size="small">取消</el-button>
            </router-link>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    import WyuUpload from "@/components/Photos";
    import ProductSelect from "../../product/components/ProductSelect";
    import ImgUploads from "../../../../../sqtuan/src/components/Photos";

    export default {
        name: 'manjianEdit',
        components: {ImgUploads, ProductSelect, TopNav, WyuUpload},
        data() {
            return {
                selGoods: [],
                loading: false,
                typeId: 0,
                data: {},
                goods: {},
                goodsDetail: {
                    manjian_id: 0,
                    num: 0,
                    man: 0,
                    jian: 0,
                    is_vip: 1,
                    vip_man: 0,
                    vip_jian: 0,
                    name: '',
                    prom_img: '',
                    start_time: '',
                    end_time: '',
                    is_online: 1,
                    orderby: 0,
                    seo: {
                        title: '',
                        keywords: '',
                        description: '',
                        releaseDate: '',
                        image: [],
                    },
                },

            }
        },
        created() {
            this.getDetail();
        },

        methods: {
            getGoods(val) {
                val.forEach(row => {
                    this.selGoods.unshift(row);
                });
                let newGoods = [];
                this.selGoods = this.selGoods.reduce(function (item, next) {
                    newGoods[next.goods_id] ? '' : newGoods[next.goods_id] = true && item.push(next);
                    return item;
                }, []);
            },
            getSelectIds() {
                let Ids = [];
                this.selGoods.forEach(val => {
                    Ids.push(val.goods_id);
                });
                return Ids;
            },
            remove(scope) {
                this.selGoods.splice(scope.$index, 1);
            },
            
            getDetail() {
                this.loading = true;
                api.post("/shop/admin.marketing.manjian/detail", {
                    appToken: this.$store.getters.appToken,
                    manjian_id: this.$route.params.id
                }).then(res => {
                    this.loading = false;
                    this.goodsDetail = res.detail;
                    this.selGoods = res.goods;
                }).catch(() => {
                    this.loading = false;
                });
            },
            
            //提交商品到商品库
            createGoods() {
                this.loading = true;
                api.post("/shop/admin.marketing.manjian/edit", {
                    appToken: this.$store.getters.appToken,
                    manjian_id:this.$route.params.id,
                    goods_ids: JSON.stringify(this.getSelectIds()),
                    num: this.goodsDetail.num,
                    name: this.goodsDetail.name,
                    prom_img: this.goodsDetail.prom_img,
                    man: this.goodsDetail.man,
                    jian: this.goodsDetail.jian,
                    vip_man: this.goodsDetail.vip_man,
                    vip_jian: this.goodsDetail.vip_jian,
                    start_time: this.goodsDetail.start_time,
                    end_time: this.goodsDetail.end_time,
                    is_online: this.goodsDetail.is_online,
                    orderby: this.goodsDetail.orderby,
                    is_vip: this.goodsDetail.is_vip,
                    seo: JSON.stringify(this.goodsDetail.seo),
                }).then(() => {
                    this.loading = false;
                    this.$message({
                        message: '提交成功',
                        type: 'success'
                    });
                }).catch(() => {
                    this.loading = false;
                });
            },
        }
    }
</script>


